.cart-deal {
    display: flex;
    overflow: hidden;

    justify-content: center;
    align-items: center;

    background-color: #ffffff;
    padding: 0 30px;
    box-sizing: border-box;
}

.cart-deal ks-img3 {
    width: 100%;
    height: 100%;
}

.cart-deal .image {
    position: relative;
    box-sizing: border-box;

    width: 180px;
    height: 180px;

    margin-right: 20px;

    transform: translateX(-20px);
}

.cart-deal .circle {
    position: absolute;
    box-sizing: border-box;
    overflow: hidden;

    width: 145px;
    height: 145px;
    padding: 10px;

    border-radius: 100%;
    background-color: white;
}

.cart-deal .fx {
    position: absolute;

    width: 250px;
    height: 250px;

    max-width: initial;
    animation-name: rotate;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
}

.cart-deal .fx1 {
    animation-duration: 50s;
}

.cart-deal .fx2 {
    animation-duration: 40s;
    animation-direction: reverse;
}

.cart-deal .fx3 {
    animation-duration: 40s;
}

.cart-deal .fx4 {
    animation-duration: 50s;
    animation-direction: reverse;
}

@keyframes rotate {
    from {transform: translate(-50%, -50%) rotate(0deg)}
    to {transform: translate(-50%, -50%) rotate(360deg)}
}

.cart-deal .center {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}


.cart-deal .text {
    font-size: 16px;
    text-align: center;
}

.cart-deal .top > a {
    display: block;
    color: #252525;
    text-decoration: none;
    margin-bottom: 8px;
}

.cart-deal .text > .bottom {
    display: flex;
    justify-content: center;
    align-items: center;

    font-size: 34px;
    font-weight: 700;
    font-family: var(--font-emphasis);
}

.cart-deal button {
    position: relative;

    margin-left: 20px;
    padding: 13px 30px 13px 30px;

    border-radius: 50px;
    border-style: none;

    font-weight: 700;
    font-size: 18px;
    width: 260px;

    background-color: var(--color-secondary);
    color: white;

    transition: background-color 0.3s;
}

.cart-deal button:not(.loading):hover {
    background-color: var(--color-secondary-hover);
}

.cart-deal button:not(.loading):active {
    background-color: var(--color-secondary-hover);
    transition: background-color 0.1s;
}

.cart-deal button.loading {
    background-color: #d3d3d3;
}

.cart-deal .variants {
    margin-bottom: 10px;
}

.cart-deal .variants label {
    display: inline-block;
    font-size: 15px;
    flex-shrink: 0;
    margin-right: 10px;
}

.cart-deal .variants ks-icon {
    transform: translateX(-30px);
    margin-right: -30px;
    pointer-events: none;
}

.cart-deal select {
    display: inline-block;
    padding: 8px 40px 10px 10px;
    height: 36px;
    min-width: 200px;

    font-size: 15px;
    appearance: none;

    background-color: #ffffff;
    border: 1px solid #d9d9d9;
    
    transition: background-color 0.3s ease;
}

.cart-deal select::-ms-expand {
	display: none;
}

.cart-deal select:hover {
    background-color: #e6e6e6;
}

.cart-deal select option {
    font-weight: normal;
}



@media only screen and (max-width: 760px) {
    .cart-deal {
        display: block;
        padding: 20px;
    }

    .cart-deal .image {
        transform: translateX(0px);
        margin: 10px auto 30px auto;
    }

    .cart-deal .variants {
        justify-content: center;
    }

    .cart-deal select {
        max-width: 220px;
        text-align: center;
        text-align-last: center;
    }
}

@media only screen and (max-width: 460px) {
    .cart-deal .text > .bottom {
        flex-direction: column;
        font-size: 34px !important;
    }

    .cart-deal button {
        margin: 5px 0 0 0;
    }
}