import { Component, h, Prop, State, Host } from '@stencil/core';
import { cart, CartDeal } from '../../../global/data/cart';
import { common } from '../../../global/data/common';
import * as cartFunctions from "../../../global/functions/cart";

@Component({
  tag: 'ks-cart-deal-group',
  styleUrl: 'deal.css'
})
export class CartDealGroup {

    @Prop({reflect: true}) ikey: string;
    @Prop() name: string;
    @Prop() deals: CartDeal[];
    @State() currentDeal: CartDeal;

    componentWillLoad() {
        this.currentDeal = this.deals[0];
    }

    change(target: EventTarget) {
        const select = target as HTMLSelectElement;
        this.currentDeal = this.deals[select.selectedIndex];
    }

    Add() {
        cartFunctions.addDeal(this.currentDeal.id);
    }

    render() {
        const translations = common.get('translations');
        const strings = cart.get('strings');

        return <Host class="cart-deal">
            <a href={this.currentDeal.link} aria-label={`Propozycja promocyjna: ${this.name} - ${this.currentDeal.price}`}>
                <div class="image">
                    <div class="circle center">
                        <ks-img3 height={125} width={125} image={this.currentDeal.img} alt={this.name} fit="contain" />
                    </div>

                    <svg class="fx fx1 center" width="250" height="250">
                        <circle cx="125" cy="125" r="80" fill="transparent" stroke="#252525" stroke-width="3" stroke-dasharray="50, 32" />
                    </svg>

                    <svg class="fx fx2 center" width="250" height="250">
                        <circle cx="125" cy="125" r="88" fill="transparent" stroke="#252525" stroke-width="3" stroke-dasharray="50,59" />
                    </svg>

                    <svg class="fx fx3 center" width="250" height="250">
                        <circle cx="125" cy="125" r="96" fill="transparent" stroke="#252525" stroke-width="3" stroke-dasharray="30,30" />
                    </svg>

                    <svg class="fx fx4 center" width="250" height="250">
                        <circle cx="125" cy="125" r="106" fill="transparent" stroke="#252525" stroke-width="3" stroke-dasharray="70,60" />
                    </svg>
                </div>
            </a>

            <div class="text">
                <div class="top">
                    <span>{this.name}</span>

                    <div class="variants">
                        <label>{strings.selectColor}</label>
                        <select onChange={event => this.change(event.target)} aria-label={strings.selectColor}>
                            {this.deals.map(deal => <option>{deal.name}</option>)}
                        </select>
                        <ks-icon name="chevron-down"/>
                    </div>
                </div>
                <div class="bottom">
                    {this.currentDeal.price}

                    <button onClick={()=>this.Add()} disabled={cart.get('loadingDeals')} class={cart.get('loadingDeals') ? "loading" : ""}>
                        {cart.get('loadingDeals') ? <ks-loader dark large/> : null}
                        <span>{translations.addToCart}</span>
                    </button>
                </div>
            </div>
        </Host>;
    }
}
